import React from 'react';
import { RxMagnifyingGlass } from 'react-icons/rx';
import styled from 'styled-components';

export const Searchbar = ({ onSubmit, searchQuery }) => {
  return (
    <StyledHeader>
      <form
        onSubmit={event => {
          onSubmit(event);
        }}
      >
        <button type="submit">
          <RxMagnifyingGlass />
        </button>

        <input
          name="searchQuery"
          type="text"
          autoComplete="off"
          autoFocus
          placeholder="Search images and photos"
        />
      </form>
    </StyledHeader>
  );
};

const StyledHeader = styled.header`
  width: 100%;
  top: 0;
  left: 0;
  position: sticky;
  z-index: 3;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 64px;
  padding-right: 24px;
  padding-left: 24px;
  padding-top: 12px;
  padding-bottom: 12px;
  color: #fff;
  background-color: #3f51b5;
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
    0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);

  form {
    display: flex;
    align-items: center;
    width: 100%;
    max-width: 600px;
    background-color: #fff;
    border-radius: 3px;
    overflow: hidden;
  }

  input {
    display: inline-block;
    width: 100%;
    font: inherit;
    font-size: 20px;
    border: none;
    outline: none;
    padding-left: 4px;
    padding-right: 4px;
    &::placeholder {
      font: inherit;
      font-size: 18px;
    }
  }

  button {
    display: inline-block;
    width: 48px;
    height: 48px;
    border: 0;
    background: transparent;
    opacity: 0.6;
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
    cursor: pointer;
    outline: none;
    margin-right: 20px;

    & svg {
      width: 40px;
      height: 40px;
    }

    &:hover {
      opacity: 1;
    }
  }
`;
